
.box{
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.wrapper{
  position: absolute;
  margin: auto;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  width: fit-content;
  text-align: center;
  z-index: 4;



  span {
    &:first-letter{
      letter-spacing: 12vmax;
    }
    position: relative;
    color: #fff;
    font-weight: 900;
    font-size: 20.4em;
    display: block;
    overflow: hidden;
    width: fit-content;
    height: max-content;
    &:before{
      content:'';
      background-image: url('https://staticdelivery.nexusmods.com/mods/1151/images/528-0-1447526230.png');
      position: absolute;
      height: 100%;
      width: 100%;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      animation: rotateIn 0.5s ease-out;
    }
  }
  p {
    text-align: center;
    font-style: italic;
    font-weight: 400;
    color: var(--color-white);
    margin-top: 0;
    line-height: 22px;
  }
}



.space {
  width: 100%;
  height: 100%;
  z-index: 1;
  animation: spin 240s linear infinite;
  background-size: 100%;
  backface-visibility: visible;
  background-image: url(data:image/svg+xml;base64,);
}